<template>
  <div class="loading-more"
    v-infinite-scroll="loadMore"
    infinite-scroll-disabled="loading"
    infinite-scroll-distance="10"
    :infinite-scroll-immediate-check="true"
    >
    <slot></slot>
    <img @click="toTop()" v-if="isBackTop" style="width:40px;height:40px;position:fixed;right:10px;bottom:50px" src="@/assets/img/backtop.png" alt="">
    <p class="page-infinite-loading" ref="spinner">
        <spinner type="fading-circle" color="#23ab1b" class="spinner"></spinner>
        加载中...
    </p>
    <div v-if="complete" style="text-align:center;padding:5px 0">
      <p v-if="info!='暂无记录!'"  style="font-size:12px">{{info}}</p>
      <img class="nolist" v-if="info=='暂无记录!'" src="../assets/img/kongzhuangtai.png" alt="">
      <h2 v-if="info=='暂无记录!'" >
        这里没有东西哦!
      </h2>
      <h4 v-if="info=='暂无记录!'" >快去逛逛吧,装点东西进来</h4>
    </div>
  </div>
</template>

<script>
import { InfiniteScroll, Spinner, Toast } from "mint-ui"
export default {
  components: {
    Spinner,
    InfiniteScroll
  },
  props: {
    isLoad: {
      type: Boolean,
      default: true
    },
    list: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      loading: false,
      morebox: "",
      isBackTop: false,
      complete: false,
      info: "数据已全部加载完毕"
    }
  },
  mounted() {
    // if (this.list.length === 0) {
    //   this.complete = true
    //   this.info = "暂无记录"
    //   this.$refs.spinner.style.display = "none"
    // } else {
    //   this.complete = false
    //   this.info = "数据已全部加载完毕"
    // }
    this.$nextTick(() => {
      this.morebox = document.getElementsByClassName("loading-more")[0]
      // 监听滚动条 大于300显示回到顶部图标
      this.morebox.onscroll = e => {
        this.morebox.scrollTop > 300
          ? (this.isBackTop = true)
          : (this.isBackTop = false)
      }
    })
  },
  methods: {
    // 滚动到底部
    loadMore() {
      if (this.isLoad) {
        this.loading = true
        this.$refs.spinner.style.display = "block"
        setTimeout(() => {
          this.loading = false
          this.$emit("is-loadmore")
        }, 2000)
      } else {
        this.loading = true
        this.complete = true
        this.$refs.spinner.style.display = "none"
        Toast({ message: "没有更多了", position: "bottom", duration: 2000 })
      }
    },
    toTop() {
      var gotoTop = () => {
        this.morebox.scrollTop -= 20
        if (this.morebox.scrollTop > 0) {
          this.morebox.scrollTo(0, this.morebox.scrollTop)
        } else {
          this.morebox.scrollTo(0, 0)
          clearInterval(timer)
        }
      }
      var timer = setInterval(gotoTop, 1)
    }
  },
  watch: {
    isLoad(ss) {
      if (ss) {
        this.isLoad = ss
        this.complete = false
        this.loading = false
        this.$refs.spinner.style.display = "block"
      } else {
        this.isLoad = ss
        this.complete = true
        this.$refs.spinner.style.display = "none"
      }
    },
    list(newL) {
      if (this.list.length === 0) {
        this.info = "暂无记录!"
        this.complete = true
        this.$refs.spinner.style.display = "none"
      } else {
        this.info = "数据已经加载完毕!"
        this.$refs.spinner.style.display = "none"
      }
    }
  }
}
</script>

<style>
.loading-more {
  height: 100%;
  width: 100%;
  overflow: auto;
}
.page-infinite-loading {
  text-align: center;
  line-height: 37px;
}
.spinner {
  display: inline-block;
  width: 28px;
  vertical-align: middle;
}
  .nolist{
    width: 2.89rem;
    height: 1.69rem;
    display: block;
    margin: 0 auto 0.68rem;

  }
h2{
  font-size:0.19rem;
  text-align: center;
  margin-bottom: 0.17rem;
  color: #333;
}
h4{
  color: #9a9a9a;
  font-size: 0.15rem;
  text-align: center;
  margin-bottom: 0.41rem;
  font-weight: normal;
}
</style>



